.height-640 {
    min-height: 640px;
    width: 100%;
    padding: 140px 0;
    @media #{$sm-layout} {
        min-height: auto;
        width: 100%;
        padding: 250px 0;
    }
}

.height-850 {
    min-height: 850px;
    width: 100%;
    padding: 240px 0 190px;
    @media #{$sm-layout} {
        min-height: auto;
        width: 100%;
        padding: 250px 0;
    }
}

.height-940 {
    min-height: 875px;
    width: 100%;
    @media #{$sm-layout} {
        min-height: auto;
        width: 100%;
        padding: 250px 0;
    }
}

.slider-area {
    padding: 0 0 100px;
    // overflow: hidden;
    height: 880px;
}

.carousel-area {
    overflow: hidden;
}

.banner-slider {
    ul.slick-dots {
        padding: 0 0 100px 0;
        margin: 0;
        bottom: -50px!important;
        li.slick-active {
            margin: 0 1px!important;
        }
        @media #{$sm-layout} {
            bottom: -50px!important;
        }
    }
    .slick-slider {
        max-width: 1600px;
        margin: auto;
        button.slick-arrow::before {
            color: var(--color-primary);
            font-size: 32px;
        }
        button.slick-prev {
            left: 12px;
            z-index: 2;
            &:before {
                content: '\002770';
            }
        }
        button.slick-next {
            right: 12px;
            z-index: 2;
            &:before {
                content: '\002771';
            }
        }
    }    
    .scroll-down-btn {
        position: relative;
        z-index: 3;
        bottom: 38px;
        cursor: pointer;
    }
}

.banner-two-mobile {
    background: url(../../images/bg/wilplus-banner_v2.png)!important;
    background-position: center bottom!important;
}

.banner-two {
    background: url(../../images/bg/wilplus-banner_v2.png)!important;
}

.banner-three-mobile {
    background: url(../../images/bg/skillsboost_banner_v3.webp)!important;
    background-position: center bottom!important;
}

.banner-three {
    background: url(../../images/bg/skillsboost_banner_v3.webp)!important;
    background-repeat: no-repeat!important;
    background-size: cover!important;
}

.banner-style-1 {
    position: relative;
    // background: linear-gradient(rgba(255,255,255,0.6), rgba(255,255,255,0.5)),url(../../../../public/images/banner/banner-01/M2M-Landing-5.png);
    background: url(../../../../public/images/banner/banner-01/M2M-Landing-5.webp);
