    .banner-left-content {
        margin-top: 120px;
        display: flex;
        align-items: center;
        @media #{$smlg-device} {
            margin-top: 0;
        }
    }

    .slider-banner-left-content {
        min-height: 570px;
    }

    .inner {
        .content {
            .pre-title {
                letter-spacing: 0.1em;
                text-transform: uppercase;
                font-weight: 800;
                font-size: 16px;
                line-height: 26px;
                color: var(--color-primary);
                display: block;
                margin-bottom: 10px;
                background-color: #fff;
                border-radius: 5px;
            }
            .title {
                font-weight: 800;
                font-size: 72px;
                line-height: 1.25;
                margin-bottom: 30px;
                
                @media #{$lg-layout} {
                    font-size: 52px;
                    margin-bottom: 20px;
                }
                
                @media #{$md-layout} {
                    font-size: 45px;
                }

                @media #{$sm-layout} {
                    font-size: 38px;
                }
            }

            .description {
                margin-bottom: 60px;
                padding-left: 20px;
                padding-right: 10px;
                position: relative;
                font-weight: 500;
                font-size: 20px;
                line-height: 32px;
                background-color: #fff;
                border-radius: 5px;

                @media #{$lg-layout} {
                    margin-bottom: 30px;
                }
                @media #{$md-layout} {
                    margin-bottom: 30px;
                }
                @media #{$sm-layout} {
                    margin: 0 auto 30px;
                    font-size: 18px;
                    line-height: 28px;
                }
                &::before {
                    position: absolute;
                    content: "";
                    left: 0;
                    width: 2px;
                    height: 73%;
                    background: var(--color-primary);
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
    }

    .work-shop {
        // min-width: 193px;
        // max-width: 211px;
        background: var(--color-white);
        padding: 12px;
        box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.05);
        border-radius: 5px;
        margin: 12px 0 0 auto;
        @media #{$lg-layout} {
            min-width: 100%;
            max-width: 100%;
            margin-right: 0;
        }

        @media #{$md-layout} {
            min-width: 100%;
            max-width: 100%;
            margin-right: 0;
        }

        @media #{$sm-layout} {
            max-width: 100%;
            display: flex;
            flex-direction: column;
            margin-right: 0;
            margin-left: 0;
            .inner {
                display: flex;
                .content {
                    // display: flex;
                    .content-description {
                        margin-left: 10px;
                        .title {
                            margin: -6px 0 0!important;
                        }
                    }
                    .edu-button {
                        width: 100%;
                    }
                }
            }
        }
        .inner {
            display: flex;
            flex-direction: row;
            .thumbnail {
                img {
                    width: 90px;
                    height: 70px;
                    object-fit: cover;
                }
            }

            .content {
                padding-left: 8px;
                .title {
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 28px;
                    margin: -6px 0 0;
                }
            }
        }
        .time {
            font-weight: 600;
            font-size: 13px;
            line-height: 22px;
            display: inline-block;
            margin-bottom: 8px;
        }
        .edu-button {
            gap: 12px;
        }
    }
    
    .shape-round {
        position: absolute;
        left: -312px;
        top: 130px;
        z-index: -1;
    }

    .scroll-down-btn {
        position: absolute;
        top: 100%;
        left: calc(50% - 30px);
        transform: translateY(-50%);
        z-index: 1;
    }

    .shape-dot-wrapper {
        .shape {
            position: absolute;
            z-index: -1;

            &.shape-1 {
                top: 97px;
                left: -189px;
            }

            &.shape-2 {
                top: 53px;
                left: 580px;
            }

            &.shape-3 {
                top: 95px;
                right: -160px;
            }

            &.shape-4 {
                bottom: 10px;
                right: -173px;
            }

            &.shape-5 {
                bottom: -23px;
                left: 654px;
            }

            &.shape-6 {
                bottom: -37px;
                left: -255px;
            }
        }
    }

    .eduvibe-hero-one-video {
        .thumbnail {
            margin: 30px -149px 0 -23px;
            @media #{$custom-laptop-device} {
                margin: 0 -70px 0 -30px;
            }
            @media #{$smlg-device} {
                // margin: 0 -30px 0 -30px;
                margin: 0;
            }
            @media #{$large-mobile} {
                // margin: -30px 0 0 -30px;
            }
            img {
                max-width: fit-content;
            }
        }
    }
}

