/*-------------------------
    Banner Style Two  
--------------------------*/
.banner-style-2 {
    background-image: url(../../images/bg/home-two-banner.jpg);
    position: relative;
    z-index: 1;
    min-height: 930px;

    @media #{$smlg-device} {
        min-height: 800px;
    }

    @media #{$lg-layout} {
        padding: 0 30px;
    }

    @media #{$md-layout} {
        padding: 150px 30px;
        height: auto;
        padding-bottom: 0;
    }

    @media #{$sm-layout} {
        padding: 150px 0px;
        height: auto;
        padding-bottom: 0;
    }
    
    .inner {
        position: relative;
        margin-top: 100px;
        @media #{$smlg-device} {
            margin-top: 0px;
        }
        .content {
            .pre-title {
                letter-spacing: 0.1em;
                text-transform: uppercase;
                font-weight: 800;
                font-size: 16px;
                line-height: 26px;
                color: var(--color-primary);
                margin-bottom: 10px;
                display: block;
                padding-left: 60px;
                position: relative;

                &::before {
                    left: 0;
                    top: 13px;
                    width: 50px;
                    height: 2px;
                    background: var(--color-primary);
                    content: "";
                    position: absolute;
                }
            }

            .title {
                font-weight: 800;
                font-size: 68px;
                line-height: 1.24;
                margin-bottom: 30px;

                @media #{$lg-layout} {
                    font-size: 45px;
                }
                @media #{$md-layout} {
                    font-size: 40px;
                    margin-bottom: 15px;
                }
                @media #{$sm-layout} {
                    font-size: 38px;
                    margin-bottom: 15px;
                }
                @media #{$large-mobile} {
                    font-size: 32px;
                }
            }
            .description {
                margin-bottom: 60px;
                font-weight: 500;
                font-size: 20px;
                line-height: 32px;
                @media #{$md-layout} {
                    margin-bottom: 40px;
                }
                @media #{$sm-layout} {
                    font-size: 18px;
                    line-height: 28px;
                    margin-bottom: 25px;
                }
            }
            .arrow-sign {
                position: absolute;
                right: -30px;
                bottom: -50px;
                img {
                    max-width: 70%;
                }
                @media #{$custom-lg-device-three} {
                    right: -50px;
                    bottom: -90px;
                    img {
                        max-width: 100%;
                    }
                }

                @media #{$laptop-device} {
                    right: -50px;
                    bottom: -30px;
                    img {
                        max-width: 50%;
                    }
                }

                @media #{$lg-layout} {
                    right: -110px;
                    bottom: -12px;
                    img {
                        max-width: 40%;
                    }
                }
            }
        }
    }

    .banner-thumbnail {
        position: absolute;
        bottom: 0;
        right: 111px;
        z-index: 1;

        @media #{$laptop-device} {
            right: 31px;
        }
        img {
            max-width: calc(100% - 60px );
        }
        @media #{$custom-laptop-device} {
            right: 0;
            img.girl-thumb {
                max-width: 90%;
            }
        }

        @media #{$smlg-device} {
            right: -138px;
            img.girl-thumb {
                max-width: 80%;
            }
        }

        @media #{$md-layout} {
            position: static;
            text-align: right;
            margin-top: -110px;
            img.girl-thumb {
                max-width: 70%;
            }
        }

        @media #{$sm-layout} {
            position: static;
            img.girl-thumb {
                max-width: 100%;
            }
        }
    }

    .banner-bg {
        position: absolute;
        bottom: 3px;
        right: 67px;
        z-index: 0;

        @media #{$custom-laptop-device} {
            right: 0;
        }
        img.girl-bg {
            max-width: calc(90% - 10px);
            @media #{$custom-laptop-device} {
                max-width: 90%;
            }
        }

        @media #{$laptop-device} {
            right: -40px;
        }

        @media #{$smlg-device} {
            right: -218px;
            img.girl-bg {
                max-width: 80%;
            }
        }
    }

    .shape-image {
        position: absolute;
        z-index: -1;

        &.shape-image-1 {
            top: 231px;
            left: 50px;
            @media #{$laptop-device} {
                top: 170px;
                left: -10px;
            }
        }

        &.shape-image-2 {
            top: 240px;
            right: 43%;
        }

        &.shape-image-3 {
            top: 250px;
            right: 90px;
        }
    }
}

