            right: 48%;
            top: -32px;
        }

        &.shape-image-3 {
            right: -210px;
            top: 10px;
        }

        &.shape-image-4 {
            right: 360px;
            bottom: -90px;
        }

        &.shape-image-5 {
            left: 463px;
            bottom: 270px;
        }

        &.shape-image-6 {
            left: 150px;
            bottom: -100px;
