    .social-text-share {
        @extend %liststyle;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -175px;
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        @media #{$laptop-device} {
            left: -105px;
        }
        @media #{$custom-laptop-device} {
            left: -70px;
        }
        @media #{$smlg-device} {
            position: absolute;
            top: inherit;
            transform: none;
            left: 15px;
            display: flex;
            flex-direction: row;
            align-items: center;
            bottom: -40px;
            margin: 0 -20px;
        }
        @media #{$small-tablet} {
            bottom: -65px;
        }

        li {
            margin: 0;
            a {
                display: inline-block;
                transform: rotate(-90deg);
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
                margin: 20px 0;
                position: relative;
                @media #{$smlg-device} {
                    transform: none;
                    margin: 0px 20px;
                }
                &::before {
                    position: absolute;
                    content: "";
                    background: var(--color-primary);
                    bottom: 0;
                    width: 0;
                    opacity: 0;
                    left: 0;
                    transition: 0.4s;
                    height: 2px;
                }
                &:hover {
                    &::before {
                        width: 100%;
                        opacity: 1;
                    }
                }
            }
        }
    }
}


/*-------------------------
    Banner Style five  
--------------------------*/

.banner-style-5 {
    position: relative;
    z-index: 1;
    background-image: url(../../images/bg/home-five-banner.jpg);
    .wrapper {
        height: 860px;
        @media #{$lg-layout} {
            height: auto;
            padding-top: 120px;
        }

        @media #{$md-layout} {
            height: auto;
            padding-top: 120px;
        }

        @media #{$sm-layout} {
            height: auto;
            padding-top: 120px;
        }
    }

    .content {
        .title {
            font-weight: 800;
            font-size: 72px;
            line-height: 1.28;
            margin-bottom: 30px;
            
            @media #{$laptop-device} {
                font-size: 57px;
                margin-bottom: 20px;
            }

            @media #{$lg-layout} {
                font-size: 40px;
            }
            @media #{$md-layout} {
                font-size: 40px;
                margin-bottom: 20px;
            }
            @media #{$sm-layout} {
                font-size: 40px;
                margin-bottom: 20px;
            }
        }
        .description {
            font-weight: 500;
            font-size: 20px;
            line-height: 32px;
            margin-bottom: 60px;
            
            @media #{$lg-layout} {
                margin-bottom: 30px;
            }
            
            @media #{$md-layout} {
                margin-bottom: 30px;
            }

            @media #{$sm-layout} {
                margin-bottom: 30px;
            }
        }
        .read-more-btn {
            display: flex;
            align-items: center;
            @media #{$large-mobile} {
                display: block;
            }
        }

        .video-btn-wrapper {
            @media #{$large-mobile} {
                margin-left: 0;
                margin-top: 20px;
            }
        }
        .video-play-btn {
            width: 60px;
            height: 60px;
            background: rgba(251, 124, 86, 0.15);
            margin: inherit;
            margin-left: 30px;
            @media #{$large-mobile} {
                margin-left: 0;
                margin-top: 0;
            }
            &::before {
                display: none;
            }
            &::after {
                display: none;
            }
            .play-icon {
                &::before {
                    border-top: 9px solid transparent;
                    border-bottom: 9px solid transparent;
                    border-left: 15px solid var(--color-secondary);
                }
            }
        }
        .video-btn-wrapper {
            display: flex;
            align-items: center;
            .video-text {
                font-weight: 700;
                font-size: 18px;
                line-height: 28px;
                margin-left: 20px;
                display: inline-block;
                color: var(--color-heading);
            }
        }
    }
    .banner-image {
        position: absolute;
        bottom: 0;
        right: 13%;
        @media #{$custom-lg-device} {
            right: 4%;
        }
        @media #{$lg-layout} {
            position: relative;
            bottom: 0;
            right: 0;
        }
        @media #{$md-layout} {
            position: relative;
            bottom: 0;
            right: 0;
            text-align: center;
        }
        @media #{$sm-layout} {
            position: relative;
            bottom: 0;
            right: 0;
            text-align: center;
        }
        img {
            border-top-right-radius: 500px;
            border-top-left-radius: 500px;

            @media #{$md-layout} {
                width: 100%;
            }
            @media #{$sm-layout} {
                width: 100%;
            }
        }

        .card-info {
            position: absolute;
            left: -10%;
            top: 19%;
            @media #{$md-layout} {
                left: 0;
            }
            @media #{$sm-layout} {
                left: 10px;
                top: auto;
                bottom: 10px;
            }
            .inner {
                background: #FFFFFF;
                box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.06);
                border-radius: 5px;
                padding: 20px 30px;

                .name {
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 28px;
                    color: var(--color-heading);

                    span {
                        font-size: 14px;
                        color: var(--color-body);
                        font-weight: 400;
                    }
                }

                .rating-wrapper {
                    span {
                        display: inline-block;
                        margin-left: 10px;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 26px;
                    }
                }
            }

        }
    }

    .shape-image {
        position: absolute;
        z-index: -1;

        &.shape-image-1 {
            bottom: 235px;
            left: 168px;
            animation: eduvibe-vsm-y-move 3.5s alternate infinite linear;
            @media #{$custom-lg-device} {
                left: 28px;
            }
            @media #{$custom-laptop-device} {
                display: none;
            }
        }

        &.shape-image-2 {
            top: 183px;
            left: 73px;
            @media #{$custom-laptop-device} {
                top: 160px;
                left: -40px;
            }
        }

        &.shape-image-3 {
            top: 180px;
            left: 52%;
        }

        &.shape-image-4 {
            left: 42%;
            bottom: 370px;
            @media #{$custom-laptop-device} {
                bottom: 200px;
            }
        }

        &.shape-image-5 {
            bottom: 116px;
            left: 49%;
        }

        &.shape-image-6 {
            left: 50px;
            bottom: 90px;
        }
    }
}

/*-------------------------
    Banner Style Six  
--------------------------*/

.banner-style-6 {
    position: relative;
    z-index: 2;

    .scroll-down-btn {
        position: absolute;
        bottom: 30px;
