                left: 40px;
            }
    
            &.gallery-image-3 {
                left: 120px;
                bottom: 150px;
            }
    
            &.gallery-image-4 {
                bottom: 290px;
                right: 0px;
            }
    
            &.gallery-image-5 {
                bottom: 170px;
                right: 120px;
            }
    
            &.gallery-image-6 {
                right: 0;
